import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { ApiGet, ApiPost } from '../api'
import NavBar from '../navbar'


let dataToSend = null

export default function Options() {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [dataItems, setData] = useState({})
  const token = useSelector((state) => state.token)
  dataToSend = dataItems


  useEffect(() => {
    if (token === '') navigate('/signin')
  }, [token])

  const syncData = async () => {
    const h = {
      Accept: 'application/json',
      'Token': token,
    }
    const res = await ApiGet('/options', h)
    if (res.ok === true) {
      console.log(JSON.stringify(res.data.data))
      setData(res.data.data)
    }
    if (res.status === 403) {
      dispatch({ type: 'set', token: '' })
      navigate("/signin")
    }
  }

  useEffect(() => {
    console.log('useEffect Options')
    syncData()
  }, [])

  const updateField = async (k, v) => {
    dataToSend[k] = v.target.value
  }

  const kirimOptions = async () => {
    const h = {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      'Token': token,
    }
    const res = await ApiPost('/options/update', dataToSend, h)
    if (res.ok === true) {
      alert('Data Konfigurasi BERHASIL disimpan')
    } else {
      alert('Data Konfigurasi GAGAL disimpan')
    }
  }

  return (
    <>
      <NavBar />
      <div className="container pt-4">
        <h1 className="h2 mb-4">Konfigurasi</h1>
        <form>{Object.keys(dataItems).map(function (key, idx) {
          return (
            <div className="mb-3" key={idx}>
              <label htmlFor={key} className="form-label">{key}</label>
              <input type="text" className="form-control" id={key} placeholder={key} defaultValue={dataItems[key]} onChange={(d) => updateField(key, d)} />
            </div>
          )
        })}
          <button type="button" onClick={kirimOptions} className="btn btn-primary">Simpan</button>
        </form>
      </div>
    </>
  )
}