import { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate } from "react-router-dom"
import { ApiPost } from "../api"
import Navbar from "../navbar"
import { Parsingobj } from "./parsingobj"

let lastreloadLog = 0

export default function Logging() {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [dataItems, setData] = useState([])
  const { loadLog: reloadLog, token } = useSelector((state) => state)

  useEffect(() => {
    syncLog()
  }, [])

  useEffect(() => {
    if (token === '') navigate('/signin')
  }, [token])

  const syncLog = async () => {
    const h = {
      'Content-Type': 'application/json',
      'Token': token,
    }
    console.log(JSON.stringify({ h }))
    const b = {
      limit: '1000',
      to: 'latest',
    }
    const res = await ApiPost('/logging', b, h)
    if (res.ok === true) {
      // console.log(JSON.stringify(res.data.data))
      setData(res.data.data)
    }
    if (res.status === 403) {
      dispatch({ type: 'set', token: '' })
      navigate("/signin")
    }
  }
  if (lastreloadLog !== reloadLog) {
    lastreloadLog = reloadLog
    syncLog()
    console.log("render Logging", reloadLog)
  }
  return (
    <>
      <Navbar />
      <div className="container pt-4">
        <h1 className="h2 mb-4">Log Aktivitas</h1>
        <div className="table-responsive">
          <table className="table">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Tanggal</th>
                <th scope="col">Log</th>
              </tr>
            </thead>
            <tbody>{dataItems.map((val, idx) => {
              return (
                <tr key={idx}>
                  <th scope="row">{val.id}</th>
                  <td>{val.tanggal}</td>
                  <td><Parsingobj params={val.log} /></td>
                </tr>
              )
            })}
            </tbody>
          </table>
        </div>
      </div>
    </>
  )
}
