export const Parsingobj = (s) => {
  const o = JSON.parse(s.params)
  let path = <></>
  if (typeof o.path === 'string')
    path = <div>{o.path}</div>

  let request = <></>
  if (typeof o.request === 'object')
    request = <div><b>REQUEST</b><br />{JSON.stringify(o.request).replace(/,/g, ', ')}</div>

  let response = <></>
  if (typeof o.response === 'object')
    response = <div><b>RESPONSE</b><br />{JSON.stringify(o.response).replace(/,/g, ', ')}</div>

  return <>
    {path}{request}{response}
  </>
}