import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { ApiPost } from "./api";
import NavBar from "./navbar";

let dataToSend = {}

export default function Signin() {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const token = useSelector((state) => state.token)
  const updateField = async (k, v) => {
    dataToSend[k] = v.target.value
  }
  useEffect(() => {
    console.log(JSON.stringify({ token }))
    if (token !== "") navigate("/")
  }, [token])

  const kirimOptions = async () => {
    const h = {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    }
    const res = await ApiPost('/signin', dataToSend, h)
    if (res.ok === true) {
      dispatch({ type: 'set', token: res.data.data })
    } else {
      alert(res.data.data)
    }
  }

  return (
    <React.Fragment>
      <NavBar />
      <div className="container pt-4">
        <h1 className="h2 mb-4">Sign In</h1>
        <form style={{ maxWidth: 400 }}>
          <div className="mb-3">
            <label htmlFor="Username" className="form-label">Username</label>
            <input type="text" className="form-control" id="Username" placeholder="Username" defaultValue="" onChange={(d) => updateField("User", d)} />
          </div>
          <div className="mb-3">
            <label htmlFor="Password" className="form-label">Password</label>
            <input type="password" className="form-control" id="Password" placeholder="Password" defaultValue="" onChange={(d) => updateField("Password", d)} />
          </div>
          <button type="button" onClick={kirimOptions} className="btn btn-primary">Masuk</button>
        </form>
      </div>
    </React.Fragment>
  )
}