const { create } = require('apisauce')

export const ENDPOINT = 'ws://192.168.87.183:50223/ws'
const baseURL = 'http://192.168.87.183:50223'
// export const ENDPOINT = (window.location.protocol === 'https:' ? 'wss://' : 'ws://') + window.location.host + '/ws'
// const baseURL = ''
let headers = {
  Accept: 'application/json',
}

const api = create({
  baseURL,
  headers,
})

export const ApiPost = async (path, data, h) => {
  headers = {
    ...headers,
    ...h,
  }
  console.log(JSON.stringify({ headers }))
  return await api.post(path, data, { headers })
}

export const ApiGet = async (path, h) => {
  headers = {
    ...headers,
    ...h,
  }
  console.log(JSON.stringify({ headers }))
  return await api.get(path, null, { headers })
}
