import { Link } from "react-router-dom"

export default function NavBar() {
  const location = window.location.pathname
  let title = 'App Module'
  if (typeof document.title === 'string' && document.title!=='') {
    title=document.title
  } 
  return (
    <nav className="navbar navbar-expand-lg sticky-top navbar-dark bg-dark">
      <div className="container-fluid">
        <a className="navbar-brand" href="/">{title}</a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav">
              <li className="nav-item">
                <Link aria-current="page" className={location === '/' ? 'nav-link active' : 'nav-link'} to="/" >Home</Link>
                {/* <a className={location === '/' ? 'nav-link active' : 'nav-link'} aria-current="page" href="/">Home</a> */}
              </li>
              <li className="nav-item">
                <Link aria-current="page" className={location === '/konfigurasi' ? 'nav-link active' : 'nav-link'} to="/konfigurasi" >Konfigurasi</Link>
                {/* <a className={location === '/konfigurasi' ? 'nav-link active' : 'nav-link'} href="/konfigurasi">Konfigurasi</a> */}
              </li>
            </ul>
        </div>
      </div>
    </nav>
  )
}
