import { useEffect } from "react"
import { useDispatch } from "react-redux"
import { ENDPOINT } from "./api"

export default function SocketClient() {
  const dispatch = useDispatch()
  let client = {
    readyState: 0
  }

  const connectingWs = async (init) => {
    if (init !== true) {
      await new Promise(resolve => setTimeout(resolve, 1000))
    }
    while (client.readyState !== 1) {
      client = new WebSocket(ENDPOINT)
      await new Promise(resolve => setTimeout(resolve, 1000))
      // console.log(JSON.stringify({ readyState: client.readyState }))
    }
    client.onopen = () => {
      console.log('WebSocket Client Connected')
    }
    client.onmessage = (message) => {
      console.log(JSON.stringify({ message }))
      const skr = Math.random()
      dispatch({ type: 'set', loadLog: skr })
    }
    client.onclose = async (ev) => {
      console.log('Socket is closed. Reconnect will be attempted in 5 second.', ev.reason);
      await new Promise(resolve => setTimeout(resolve, 5000))
      connectingWs()
    }
    client.onerror = function (err) {
      console.error('Socket encountered error: ', err.message, 'Closing socket')
      client.close()
    }
  }
  useEffect(() => {
    connectingWs(true)
  }, [])
  return <></>
}